body, html {
  width: 100%;
  height: 100%;
  overflow: hidden;
  margin: 0;
  padding: 0;
  line-height: inherit;
  -webkit-text-size-adjust: none;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: transparent;
  font-family: sans-serif; }

.logo {
  position: fixed;
  left: 80px;
  top: 80px;
  min-height: 54px;
  min-width: 48.5px;
  -webkit-transform-origin: left;
  -ms-transform-origin: left;
  transform-origin: left; }
  @media only screen and (max-width: 1280px) {
    .logo {
      left: 120px;
      top: 120px; } }
  @media only screen and (min-width: 1280px) and (max-width: 2560px) {
    .logo {
      left: 160px;
      top: 160px;
      min-height: 75.6px;
      min-width: 67.9px; } }
  @media only screen and (max-width: 960px) {
    .logo {
      left: 40px;
      top: 40px;
      min-height: 43.2px;
      min-width: 38.8px; } }
  @media only screen and (max-width: 640px) {
    .logo {
      left: 20px;
      top: 20px;
      min-height: 32.4px;
      min-width: 29.1px; } }
  .logo #svgLogo {
    position: absolute; }
  .logo.appear_before {
    margin-left: -50px;
    opacity: 0; }
  .logo.appear_after {
    margin-left: 0;
    opacity: 1; }
  .logo.leave_animation {
    margin-left: -50px !important;
    opacity: 0 !important; }



